.grid-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5px;
  margin-bottom: 10px;
}
@media only screen and (min-width: 768px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
}

@media only screen and (min-width: 1536px) {
  .grid-container {
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }
}
